@use "../../styles/mixins" as m;

.spinner-square {
  display: flex;
  flex-direction: row;
  width: 90px;
  height: 120px;
}

.spinner-square > .square {
  width: 17px;
  height: 80px;
  margin: auto auto;
  border-radius: 4px;
}

.square-1 {
  animation: square-anim 1200ms cubic-bezier(0.445, 0.05, 0.55, 0.95) 0s
    infinite;
}

.square-2 {
  animation: square-anim 1200ms cubic-bezier(0.445, 0.05, 0.55, 0.95) 200ms
    infinite;
}

.square-3 {
  animation: square-anim 1200ms cubic-bezier(0.445, 0.05, 0.55, 0.95) 400ms
    infinite;
}

@keyframes square-anim {
  0% {
    height: 80px;
    background-color: rgb(113, 128, 128);
  }
  20% {
    height: 80px;
  }
  40% {
    height: 120px;
    background-color: rgb(103, 116, 124);
  }
  80% {
    height: 80px;
  }
  100% {
    height: 80px;
    background-color: rgb(112, 117, 123);
  }
}

.loading-bar {
  @include m.absolute-center();
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  position: absolute;
  z-index: 10000;
  top: 17.5%;
  justify-content: center;
  align-items: center;
  &__text {
    color: #fff;
    font-size: 2rem;
    order: 1;
  }
}
